import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';
import { Component, h, Element, State, Host, Listen } from '@stencil/core';

export interface IToggleCellParams {
    getLabel: (params: ICellRendererParams) => string;
    onTrigger: (params: ICellRendererParams) => void;
    getDisabled?: (data: any) => boolean;
}

interface InitParams extends IToggleCellParams, ICellRendererParams {}

/**
 * @category Table Cell
 * @description A cell component for displaying a toggle.
 */

@Component({
    tag: 'atui-toggle-cell',
    shadow: false,
})
export class AtuiToggleCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: InitParams;
    timer: any;

    init(params: InitParams): void {
        this.params = params;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    @Listen('toggleFunction')
    toggleHandler(change: CustomEvent) {
        clearTimeout(this.timer);
        this.params.value = change.detail;
        this.timer = setTimeout(() => {}, 2000);
    }

    private updateCell(newValue: any) {
        clearTimeout(this.timer);
        this.params.value = newValue;
        this.timer = setTimeout(() => {}, 2000);
    }

    render() {
        return (
            <Host class="flex h-full items-center leading-[100%]">
                <atui-toggle-switch
                    label={this.params.getLabel(this.params)}
                    onChange={() => this.params.onTrigger(this.params)}
                    label_position="after"
                    value={this.params.value}
                ></atui-toggle-switch>
            </Host>
        );
    }
}
